<template>
    <div>
    <h1>My record collection</h1>
    <br><br><br>
    <div class="outer">
      <record-card v-for="record in recordCollection"
        :key="record.id"
        v-bind:id="record.id"
      ></record-card>
    </div>
  </div>
</template>

<script>
export default {
  props: ["id"],
  data() {
    return {};
  },
  computed: {
    recordCollection() { 
      return this.$store.getters.recordCollection;
    }
  },
  async mounted() {
    await this.$store.dispatch('refreshRecordCollection');
  }
};
</script>

<style scoped>
.outer {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  text-align: left;
  gap: 20px;
}
</style>