import { createStore } from "vuex";

export default createStore({
  state: {
    user: null,
    recordCollection: []
  },
  mutations: {
    setUser(store, newUser) {
      store.user = newUser;
    },
    setRecordCollection(store, recordCollection) {
      store.recordCollection = recordCollection
    }
  },
  getters: {
    user(store) {
      return `${store.user}`;
    },
    isAuth(store) {
      return !!store.user;
    },
    recordCollection(store) {
      return store.recordCollection || [];
    },
    getRecordById: (state, getters) => (id) => {
      return getters.recordCollection.find(rcd => rcd.id === id);
    }
  },
  actions: {
    async refreshRecordCollection(context) {
      if (context.getters.recordCollection.length === 0) {
        console.log("fetching your record colleciton");
        try {
          const production  = 'https://recordcollection-web2.herokuapp.com';
          const development = 'http://localhost:8888';
          const url = (process.env.NODE_ENV ? production : development);
          let response = await fetch(`${url}/recordCollection`);
          if (response.ok) {
            let recordCollection = await response.json();
            context.commit("setRecordCollection", recordCollection);
          } else {
            throw new Error("HTTP-Error: " + response.status);
          }
        } catch (error) {
          console.error(error);
        }
      }
    },
    deleteRecord(context, {id}) {
      context.commit("setRecordCollection", context.getters.recordCollection.filter((record) => record.id !== id));      
    },
  },
  modules: {},
});
