<template>
    <div class="card">
        <img :src="record.cover" alt="cover" style="width:100%">
        <div class="container">
            <h4><b>{{ record.title }}</b></h4>
            <p>{{ record.artist }}</p>
            <button class="trashButton" @click="deleteRecord"><img src="https://assets.webiconspng.com/uploads/2017/01/Red-Trash-Transparent-Icon.png" alt="trash" height="15" width="15"></button>
        </div>
    </div>
</template>

<script>
export default {
emits: ["deleteRecord"], 
props: ["id"],
data() {
    return {
      record: {}
    }
},
methods: {
  async deleteRecord() {
    await this.$store.dispatch('deleteRecord', { id: this.id });
    this.$emit('deleteRecord', { id: this.id });
  },
}, 
async created() {
    this.record = {... await this.$store.getters.getRecordById(this.id) };    
  }
};
</script>

<style scoped>
    .card {
      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
      transition: 0.3s;
      width: 20%;
      border-radius: 5px;
    }

    .card:hover {
      box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    }

    img {
      border-radius: 5px 5px 0 0;
      aspect-ratio: 1/1;
    }

    .container {
      padding: 2px 16px;
    }

    .trashButton {
      background-color: transparent;
      border: none;
      cursor: pointer;
      float: right; 
      margin-bottom: 5px;
    }

    .trashButton:active {
      transform: translateY(3px);
    }

</style>
