<template>
    <div>
        <div class="formDiv">
            <form>
                <label for="byear">Year when was the record bought</label>
                <input type="number" id="byear" name="byear" placeholder="Buy year" v-model="buyYear">

                <label for="bprice">Price when it was bought for</label>
                <input type="number" id="bprice" name="bprice" placeholder="Buy price" v-model="buyPrice">

                <label for="syear">Year when you want to sell the record</label>
                <input type="number" id="syear" name="syear" placeholder="Sell year" v-model="sellYear" @keyup="calculatePrice">
            
                <label for="sprice">Price for sale</label>
                <input type="number" id="sprice" name="sprice" placeholder="Sell price" v-model="sellPrice" @keyup="calculateSellYear">
            </form>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
        buyYear: 0,
        buyPrice: 0,
        sellYear: 0,
        sellPrice: 0,
    };
  },
  methods: {
    calculatePrice() {
      this.sellPrice = Math.pow((parseFloat(this.sellYear) / parseFloat(this.buyYear)), 25) * parseFloat(this.buyPrice);
    },
    calculateSellYear() {
      this.sellYear = Math.pow((parseFloat(this.sellPrice) / parseFloat(this.buyPrice)), parseFloat(1) / 25) * parseFloat(this.buyYear);
    },
  },
};
</script>


<style scoped>
input[type=number], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type=submit] {
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: #45a049;
}

.formDiv {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}
</style>